.resilts_list {
    width: 100%;
    backdrop-filter: blur(4px);
    color: rgba(52, 114, 170, 1);
    /* Размытие фона и градиент */
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.10) 100%);
    /* Внутренняя тень */
    box-shadow: inset 8px 8px 9px rgba(255, 255, 255, 0.2);
    /* Вы можете изменить толщину границы по желанию */
    border-image: linear-gradient(90deg, rgba(255, 255, 255, 0.30) 0%, #B4C5D8 48%, rgba(255, 255, 255, 0.30) 100%);
    border-image-slice: 1;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 8px #ddd;
    border-radius: 10px;
    margin-top: 1rem;
    max-height: 300px;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    position: absolute;

    ::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}